import { NGL3dApp as NGL3dAppStarter } from './NGL3dApp_01';
import { NGL3dApp } from './NGL3dApp_07';
export class AppNGL3d {
    static InitStarter(id: string) {
        new NGL3dAppStarter(id);
    }
    static Init(id: string, index: number) {
        const app = new AppNGL3d(id, index);
        
        app.Dummy();
    }
    constructor(id: string, index: number) {
        this.AppEnvCanvas(id, `NGL3dApp_${formatTwoDigits(index)}`)
    }
    async AppEnvCanvas(id: string, path: string) {
        try {
            const module = await import(`./${path}`);
            const { NGL3dApp } = module;
    
            new NGL3dApp(id);

  
        } catch (err) {
            console.log('Stop loading Apps.');
            console.log(err);
        }
    }
    public Dummy() {
        //
    }
    public BindEvent() {
        //
    }
}
const formatTwoDigits = (number: number): string => {
    return number < 10 ? `0${number}` : `${number}`;
}