import { NGL3dApp } from '..';

export class Canvas {
    app: NGL3dApp;

    gl: WebGL2RenderingContext;
    canvas: HTMLCanvasElement;

    constructor(app: NGL3dApp) {
        this.app = app;

        this.canvas = document.createElement('canvas');
        if (this.app.host) {
            this.app.host.appendChild(this.canvas);
            this.canvas.width = this.app.host.clientWidth;
            this.canvas.height = this.app.host.clientHeight;
        }
        const gl = this.canvas.getContext('webgl2');
        if (gl !== null) { this.gl = gl; }

        this.loop(this.gl);
    }
    loop(gl: WebGL2RenderingContext) {
        this.clear(gl);
        // requestAnimationFrame(() => { this.loop(gl); });
        // this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
        // this.app.Loop(this.ctx);
        // console.log('working');
    }
    clear(gl: WebGL2RenderingContext) {
        gl.clearColor(0.5, 1.0, 1.0, 1.0);
        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
    }
}
